import { BASE_IMAGE_URL,getNoShuiYin } from "@/utils/api";
import DownloadSaveFile  from "@/utils/downloadSaveFile";
import uniStatusBar from '@/components/mini_status_bar.vue';


export default {
	components: {
		uniStatusBar
	},
	data() {
		return {
			contentList: [
				{b_icon: BASE_IMAGE_URL+'video.png', b_name: '视频去水印', url: '/pages/video/video'}
			],
			scrollHeightVal: 0,
			bannerInfoList: [],
			titleStr: '视频去水印',
			pingtai: BASE_IMAGE_URL+'pingtai.png',
			clear: BASE_IMAGE_URL+'clear.png',
			videoUrl: '',
			trueVideoUrl: ''
		}
	},
	methods: {
		copyUrl(){
			uni.setClipboardData({
				data: this.trueVideoUrl
			});
			uni.showToast({
				title: '复制视频链接成功',
				duration: 1000,
				icon: 'none'
			})
		},
		saveVideo(){
			DownloadSaveFile.downloadFile('video', this.trueVideoUrl); //video或image
		},
		backAction: function(){
			uni.navigateBack();
		},
		clearUrl(){
			this.videoUrl = "";
		},
		async getVideoUrl(){
			if(!this.videoUrl){
				uni.showToast({
					title: '请输入视频地址',
					duration: 1000,
					icon: 'none'
				})
				return;
			};
			
			uni.showLoading({
				title: '视频提取中...'
			});
			let res = await getNoShuiYin(this.videoUrl);
			if(res.code == 200){
				this.trueVideoUrl = res.msg.url;
				uni.hideLoading();
			}
		}
		
	},
	onLoad() {
		this.scrollHeightVal = this.initStatus();
	},
	onShareAppMessage(res) {
		return {
			title: '与你分享，一起使用',
			path: '/pages/main/main'
		}
	},
	onShow() {
		let that = this;
		uni.getClipboardData({
			success: function(res) {
				if(res.data){
					uni.showModal({
						title: '是否复制粘贴板内容',
						content: res.data,
						success(result) {
							if(result.confirm){
								that.videoUrl = res.data;
							};
							uni.setClipboardData({
								data: '',
								complete() {
									uni.hideToast();
								}
							})
						}
					})
				}
				
			}
		});
	}
}
